.cozy-block-icon-list .cozy-block-list-item {
  display: flex;
  align-items: center;
}
.cozy-block-icon-list .cozy-block-list-item p {
  display: inline-block;
  margin: 0;
  width: 100%;
}
.cozy-block-icon-list ul,
.cozy-block-icon-list {
  list-style: none;
}
.cozy-block-icon-list a {
  text-decoration: none !important;
}
.cozy-block-icon-list .list-icon-wrapper {
  display: flex;
}
.cozy-block-icon-list.horizontal .list-inline-block {
  display: inline-block;
}

.cozy-block-icon-list ul, .cozy-block-icon-list {
  margin: 0 !important;
  padding: 0 !important;
}
